body,
html,
h1,
p {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Rubik';
    color: white;
    overflow-x: hidden;
}

h1 {
    font-weight: 500;
}

.logo {
    width: 137px;
    height: 34px;
    display: block;
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    margin: 0 auto;
    background-position: center;
    background-image: url(images/logo.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.animation {
    position: fixed;
    top: 0;
    z-index: 3;
    min-height: 100vh;
    width: 100%;
    height: 100%;
}

.circle {
    position: relative;
    background-image: url(images/circle.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.avatar {
    position: absolute;
    top: 6.5%;
    left: 6.5%;
    width: calc(100% - 16%);
    height: calc(100% - 16%);
    border-radius: 50%;
    background-repeat: no-repeat;
    background-size: cover;
}

.avatar--empty {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 40px;
    background: linear-gradient(50.62deg, #0085FF 12.57%, #0C26AE 86.74%);
}

.start, .share {
    width: 300px;
    height: 107px;
    background-repeat: no-repeat;
    background-size: contain;
}

.start, .share {
    width: 300px;
    height: 107px;
    background-repeat: no-repeat;
    background-size: contain;
}

.start {
    margin: 40px -30px 0 0;
    background-image: url(images/start.svg);
}

.share {
    width: 260px;
    height: 93px;
    margin: 20px -30px 0 0;
    background-image: url(images/share.svg);
}

.retry {
    padding-bottom: 20px;
    font-size: 19px;
    text-transform: uppercase;
    font-family: 'Open Sans';
}

.home {
    background: linear-gradient(50.14deg, #3300FF 8.11%, #00C2FF 91.07%);
}

.home_content {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.home::after {
    content: '';
    position: absolute;
    z-index: 0;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-image: url(images/background.svg);
    background-repeat: no-repeat;
    background-size: cover;
}

.home h1 {
    margin-top: 85px;
    font-size: 48px;
}

.home p {
    display: block;
    width: 70%;
    margin-top: 10px;
    text-align: center;
    font-size: 30px;
}

.home_circles {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 30px;
}

.home_circles span {
    margin: 0 15px;
    font-size: 40px;
}

.home_circle {
    width: 120px;
    height: 120px;
}

.parallelogram {
    position: relative;
    display: inline-block;
    padding: 9px;
    color: white;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.parallelogram > div {
    z-index: 1;
    display: block;
    position: relative;
}

.parallelogram::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #10263C;
    transform: skewX(-12deg);
    border-radius: 3px;
    z-index: 0;
}

.result {
    background-repeat: no-repeat;
    background-size: cover;
}

.result_content {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.result--amsterdam {
    background-image: url(images/amsterdam.png)
}

.result--china {
    background-image: url(images/china.png)
}

.result--everest {
    background-image: url(images/everest.png)
}

.result--kaluga {
    background-image: url(images/kaluga.png)
}

.result--london {
    background-image: url(images/london.png)
}

.result--mongolia{
    background-image: url(images/mongolia.png)
}

.result--munich {
    background-image: url(images/munich.png)
}

.result--paris {
    background-image: url(images/paris.png)
}

.result--pisa {
    background-image: url(images/pisa.png)
}

.result--prague {
    background-image: url(images/prague.png)
}

.result--saintp {
    background-image: url(images/saintp.png)
}

.result--taiga {
    background-image: url(images/taiga.png)
}

.result--tbilisi {
    background-image: url(images/tbilisi.png)
}

.result_circles {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 85px;
    margin-bottom: 20px;
}

.result_circle {
    width: 130px;
    height: 130px;
}

.result_circles > .result_circle:nth-child(2) {
    margin-left: -30px;
}

.result_names {
    width: 100%;
    padding: 9px;
    color: white;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #10263B;
    background-color: #ffffff;
    text-align: center;
}

.result_names, .result_text {
    font-size: 21px;
    font-weight: bold;
}

.result_label {
    padding-bottom: 5px;
    font-size: 13px;
    font-family: 'Open Sans';
}

.result_text--cutted {
    padding-top: 0
}

.hidden {
    opacity: 0;
}
