.result_circles {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 85px;
    margin-bottom: 20px;
}

.result_circle {
    width: 130px;
    height: 130px;
}

.result_circles > .result_circle:nth-child(2) {
